import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/home.vue'
import Chat from '../views/chat.vue'
import Users from '../views/users.vue'
import Settings from '../views/settings.vue'
import Eula from '../views/eula.vue'
import AGB from '../views/agb.vue'
import Impressum from '../views/impressum.vue'
import Datenschutz from '../views/datenschutz.vue'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/chat',
        name: 'Chat',
        component: Chat
    },
    {
        path: '/settings',
        name: 'Settings',
        component: Settings
    },
    {
        path: '/users',
        name: 'Users',
        component: Users
    },
    {
        path: '/eula',
        name: 'Eula',
        component: Eula
    },
    {
        path: '/agb',
        name: 'AGB',
        component: AGB
    },
    {
        path: '/impressum',
        name: 'Impressum',
        component: Impressum
    },
    {
        path: '/datenschutzerklrung',
        name: 'Datenschutz',
        component: Datenschutz
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

export default router

