<template>
  <v-card>
    <v-list>
      <v-subheader>Settings</v-subheader>

      <v-list-item-group>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Username</v-list-item-title>
            <v-list-item-subtitle>Select your chat.</v-list-item-subtitle>
            <v-text-field
              @change="save(), getID()"
              v-model="username"
            ></v-text-field>
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Font size</v-list-item-title>
            <v-list-item-subtitle>Select the font size.</v-list-item-subtitle>
            <v-slider @change="save" v-model="font" thumb-label></v-slider>
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-content>
            <v-list-item-title>Whitelist Emotes</v-list-item-title>
            <v-list-item-subtitle
              >Whitelist special emotes</v-list-item-subtitle
            >
            <v-text-field
              @change="save"
              v-model="filterwhitelist"
              placeholder="Kappa, profwaKappa"
              thumb-label
            ></v-text-field>
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="tts"></v-switch>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>Text-to-Speech</v-list-item-title>
            <v-list-item-subtitle
              >Use Text-to-Speech to read the chat on your screen
              aloud.</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="filter"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Filter Messages</v-list-item-title>
            <v-list-item-subtitle
              >Filter messages that contain emotes.</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="filterurl"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>URL Filter</v-list-item-title>
            <v-list-item-subtitle
              >Filter messages that contain URLs</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="readalertsfilter"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Read Alerts</v-list-item-title>
            <v-list-item-subtitle
              >Read events (Subs, Resubs, Cheers,
              Subgifts)</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="filterbots"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Filter Chatbot</v-list-item-title>
            <v-list-item-subtitle
              >Filter messages sended by a bot.</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="showstats"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Show stats.</v-list-item-title>
            <v-list-item-subtitle>Display current stats.</v-list-item-subtitle>
          </v-list-item-content>
        </v-list-item>

        <v-list-item>
          <v-list-item-action>
            <v-switch @change="save" v-model="alertsound"></v-switch>
          </v-list-item-action>
          <v-list-item-content>
            <v-list-item-title>Alert sound.</v-list-item-title>
            <v-list-item-subtitle
              >Play a sound on (Subs, Resubs, Cheers,
              Subgifts)</v-list-item-subtitle
            >
          </v-list-item-content>
        </v-list-item>
      </v-list-item-group>
    </v-list>
  </v-card>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    tts: false,
    showstats: false,
    filter: false,
    filterbots: false,
    filterurl: false,
    filterwhitelist: "",
    readalertsfilter: false,
    alertsound: false,
    userid: "",
    font: "",
    username: "",
  }),
  methods: {
    save() {
      localStorage.setItem("tts", this.tts);
      localStorage.setItem("showstats", this.showstats);
      localStorage.setItem("filter", this.filter);
      localStorage.setItem("filterurl", this.filterurl);
      localStorage.setItem("font", this.font);
      localStorage.setItem("filterbots", this.filterbots);
      localStorage.setItem("filterwhitelist", this.filterwhitelist);
      localStorage.setItem("readalertsfilter", this.readalertsfilter);
      localStorage.setItem("alertsound", this.alertsound);
      localStorage.setItem("username", this.username.toLowerCase());
    },
    async getID() {
      let username = localStorage.getItem("username");
      let uri = `https://cybercentralchatapi.glitch.me/twitchuser/${username}`;
      try {
        const response = await axios.get(uri);
        localStorage.setItem("userid", response.data.users[0]._id);
      } catch (error) {
        this.chatters = "API ERROR";
      }
    },
  },
  created() {
    if (!localStorage.getItem("tts")) {
      localStorage.setItem("tts", false);
    }
    if (!localStorage.getItem("filterwhitelist")) {
      localStorage.setItem("filterwhitelist", "");
    }
    if (!localStorage.getItem("showstats")) {
      localStorage.setItem("showstats", false);
    }
    if (!localStorage.getItem("readalertsfilter")) {
      localStorage.setItem("readalertsfilter", false);
    }
    if (!localStorage.getItem("filter")) {
      localStorage.setItem("filter", false);
    }
    if (!localStorage.getItem("filterbots")) {
      localStorage.setItem("filterbots", false);
    }
    if (!localStorage.getItem("username")) {
      localStorage.setItem("username", "CyberCentral");
    }
    if (!localStorage.getItem("filterurl")) {
      localStorage.setItem("filterurl", false);
    }
    if (!localStorage.getItem("font")) {
      localStorage.setItem("font", 15);
    }
    if (!localStorage.getItem("alertsound")) {
      localStorage.setItem("alertsound", false);
    }
    this.tts = JSON.parse(localStorage.getItem("tts"));
    this.showstats = JSON.parse(localStorage.getItem("showstats"));
    this.alertsound = JSON.parse(localStorage.getItem("alertsound"));
    this.filterwhitelist = localStorage.getItem("filterwhitelist");
    this.filterbots = JSON.parse(localStorage.getItem("filterbots"));
    this.filter = JSON.parse(localStorage.getItem("filter"));
    this.font = JSON.parse(localStorage.getItem("font"));
    this.filterurl = JSON.parse(localStorage.getItem("filterurl"));
    this.readalertsfilter = JSON.parse(
      localStorage.getItem("readalertsfilter")
    );
    this.username = localStorage.getItem("username");
  },
};
</script>

