<template>
  <v-app id="inspire">
    <v-navigation-drawer v-model="drawer" app>
      <template>
        <v-card class="mx-auto" max-width="300" tile>
          <v-list dense>
            <div v-if="logged == true">
              <v-list>
                <v-list-item>
                  <v-list-item-avatar>
                    <v-img :src="userdata.users[0].logo"></v-img>
                  </v-list-item-avatar>
                </v-list-item>

                <v-list-item
                  :href="`https://www.twitch.tv/${userdata.users[0].display_name}`"
                  link
                >
                  <v-list-item-content>
                    <v-list-item-title class="text-h6">
                      {{ userdata.users[0].display_name }}
                      <v-icon>mdi-twitch</v-icon>
                    </v-list-item-title>
                    <v-list-item-subtitle class="text-wrap">{{
                      userdata.users[0].bio
                    }}</v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-list>
            </div>
            <v-list-item-group v-model="selectedItem">
              <v-list-item v-for="(item, i) in items" :key="i" :to="item.link">
                <v-list-item-icon>
                  <v-icon v-text="item.icon"></v-icon>
                </v-list-item-icon>
                <v-list-item-content>
                  <v-list-item-title v-text="item.text"></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </template>
    </v-navigation-drawer>

    <v-app-bar app>
      <v-app-bar-nav-icon @click="drawer = !drawer"></v-app-bar-nav-icon>

      <v-toolbar-title>CyberCentralChat</v-toolbar-title>
    </v-app-bar>
    <v-main>
      <router-view />
    </v-main>
    <footercomp />
  </v-app>
</template>

<script>
import axios from "axios";
import footercomp from "./components/footer";
export default {
  data: () => ({
    drawer: null,
    dialog: false,
    userdata: "",
    logged: false,
    selectedItem: 1,
    items: [
      { text: "HOME", icon: "mdi-home", link: "/" },
      { text: "CHAT", icon: "mdi-message", link: "/chat" },
      { text: "USERS", icon: "mdi-account", link: "/users" },
      { text: "SETTINGS", icon: "mdi-cog-outline", link: "/settings" },
    ],
  }),
  methods: {
    async getUser() {
      let username = localStorage.getItem("username");
      let uri = `https://cybercentralchatapi.glitch.me/twitchuser/${username}`;
      try {
        const response = await axios.get(uri);
        this.userdata = response.data;
        this.logged = true;
      } catch (error) {
        this.logged = false;
      }
    },
  },
  created() {
    if (!localStorage.getItem("tts")) {
      localStorage.setItem("tts", false);
    }
    if (!localStorage.getItem("filterwhitelist")) {
      localStorage.setItem("filterwhitelist", "");
    }
    if (!localStorage.getItem("showstats")) {
      localStorage.setItem("showstats", false);
    }
    if (!localStorage.getItem("readalertsfilter")) {
      localStorage.setItem("readalertsfilter", false);
    }
    if (!localStorage.getItem("filter")) {
      localStorage.setItem("filter", false);
    }
    if (!localStorage.getItem("filterbots")) {
      localStorage.setItem("filterbots", false);
    }
    if (!localStorage.getItem("username")) {
      localStorage.setItem("username", "CyberCentral");
    }
    if (!localStorage.getItem("filterurl")) {
      localStorage.setItem("filterurl", false);
    }
    if (!localStorage.getItem("font")) {
      localStorage.setItem("font", 15);
    }
    if (!localStorage.getItem("alertsound")) {
      localStorage.setItem("alertsound", false);
    }
    this.getUser();
  },
  components: {
    footercomp,
  },
};
</script>

