<template>
  <v-container>
    <div v-if="connected">
      <viewercount />
      <alerts style="margin-top: 2%" />
    </div>
    <div v-else>
      <div v-if="!username">
        <v-btn @click="connectChat" disabled outlined block>CONNECT</v-btn>
      </div>
      <div v-else>
        <v-btn @click="connectChat" outlined block>CONNECT</v-btn>
      </div>
    </div>

    <v-card style="margin-top: 2%">
      <div id="container" style="max-height: 500px; overflow-y: auto">
        <ul>
          <li
            v-for="message in messages"
            v-bind:key="message.id"
            :style="msgStyles"
          >
            <div :style="{ color: message.color }">
              <b>{{ message.username }}</b>
            </div>
            <div v-html="message.message" />
            <v-divider></v-divider>
          </li>
        </ul>
      </div>
    </v-card>
  </v-container>
</template>

<script>
import tmi from "tmi.js";
import axios from "axios";
import viewercount from "../components/viewercount";
import alerts from "../components/alertscomp";
export default {
  name: "Home",
  data: () => ({
    ids: 0,
    connected: false,
    messages: [],
    username: "",
    betterttv: "",
    betterttvemotes: [],
    betterttvglobalemotes: [],
    twitchemotes: "",
    ttson: false,
    filterchat: false,
    readalertsfilter: false,
    filterbots: false,
    filterwhitelist: false,
    filterurl: false,
    client: null,
    fontsize: "",
  }),
  methods: {
    readAlert(type, message) {
      let constructor = `${type} ALERT: ${message}`;
      return this.speakClient(constructor);
    },
    reloadFunc() {
      if (window.localStorage) {
        if (!localStorage.getItem("firstLoad")) {
          localStorage["firstLoad"] = true;
          this.$router.go(this.$router.currentRoute);
        } else localStorage.removeItem("firstLoad");
      }
    },
    connectChat() {
      this.connected = true;
      const opts = {
        channels: [this.username],
      };
      this.client = new tmi.client(opts);

      this.client.connect();

      if (this.readalertsfilter) {
        this.client.on(
          "subscription",
          (channel, username, method, message, userstate) => {
            this.readAlert("SUB", userstate["system-msg"], "red");
          }
        );

        this.client.on(
          "subgift",
          (channel, username, streakMonths, recipient, methods, userstate) => {
            this.readAlert("SUBGIFT", userstate["system-msg"], "red");
          }
        );

        this.client.on("cheer", (channel, userstate, message) => {
          let msg = `${userstate["display-name"]} just cheered ${userstate.bits}`;
          console.log(channel, userstate, message);
          if (!message == "") {
            if (message.includes("cheer")) {
              for (let index = 1; index < 100000; index++) {
                message = message.replace("cheer" + index, "");
              }
              msg = `${userstate["display-name"]} just cheered ${userstate.bits} bits.<br>Message: ${message}`;
            }
          }
          this.genAlert("CHEER", msg, "red");
        });

        this.client.on(
          "resub",
          (channel, username, months, message, userstate, methods) => {
            this.readAlert("RESUB", userstate["system-msg"], "red");
          }
        );

        this.client.on("raided", (channel, username, viewers) => {
          this.readAlert(
            "RAID",
            `${username} just raided ${channel.replace(
              "#",
              ""
            )} with ${viewers} viewers!`,
            "red"
          );
        });
      }

      this.client.on("message", (channel, tags, message, self) => {
        let username = tags["display-name"] + " ";
        if (tags["mod"]) {
          username = username.concat("| MODERATOR ");
        }
        if (tags["subscriber"]) {
          username = username.concat("| SUBSCRIBER ");
        }
        let constructor = {
          id: ++this.ids,
          color: tags["color"],
          message: this.formatEmotes(message, tags["emotes"]),
          isModerator: tags["mod"],
          isSubscriber: tags["subscriber"],
          username: username,
        };
        this.messages.push(constructor);
        this.readMessage(tags, message);
        this.scrollToEnd();
      });
    },
    scrollToEnd() {
      var container = this.$el.querySelector("#container");
      container.scrollTop = container.scrollHeight;
      if (this.messages.length > 40) {
        this.messages.shift();
      }
    },
    formatEmotes(text, emotes) {
      var splitText = text.split("");
      for (var i in emotes) {
        var e = emotes[i];
        for (var j in e) {
          var mote = e[j];
          if (typeof mote == "string") {
            mote = mote.split("-");
            mote = [parseInt(mote[0]), parseInt(mote[1])];
            var length = mote[1] - mote[0],
              empty = Array.apply(null, new Array(length + 1)).map(function () {
                return "";
              });
            splitText = splitText
              .slice(0, mote[0])
              .concat(empty)
              .concat(splitText.slice(mote[1] + 1, splitText.length));
            splitText.splice(
              mote[0],
              1,
              '<img class="emoticon" width="25" height="25" src="https://static-cdn.jtvnw.net/emoticons/v1/' +
                i +
                '/3.0">'
            );
          }
        }
      }
      return this.betterttvParser(splitText.join(""));
    },
    betterttvParser(text) {
      let msg = text;
      let emotes = this.betterttv;
      let globalemotes = this.betterttvglobalemotes;
      for (let index = 0; index < globalemotes.length; index++) {
        const element = globalemotes[index];
        if (msg.includes(element.code)) {
          let url = ` https://cdn.betterttv.net/emote/${element.id}/3x`;
          let img = ` <img class="emoticon" width="25" height="25" src="${url}">`;
          msg = msg.split(element.code).join(img);
        }
      }
      for (let index = 0; index < emotes.channelEmotes.length; index++) {
        const element = emotes.channelEmotes[index];
        if (msg.includes(element.code)) {
          let url = ` https://cdn.betterttv.net/emote/${element.id}/3x`;
          let img = ` <img class="emoticon" width="25" height="25" src="${url}">`;
          msg = msg.split(element.code).join(img);
        }
      }
      for (let index = 0; index < emotes.sharedEmotes.length; index++) {
        const element = emotes.sharedEmotes[index];
        if (msg.includes(element.code)) {
          let url = ` https://cdn.betterttv.net/emote/${element.id}/3x`;
          let img = ` <img class="emoticon" width="25" height="25" src="${url}">`;
          msg = msg.split(element.code).join(img);
        }
      }
      return msg;
    },
    removeEmotes(text, emotes) {
      var splitText = text.split("");
      for (var i in emotes) {
        var e = emotes[i];
        for (var j in e) {
          var mote = e[j];
          if (typeof mote == "string") {
            mote = mote.split("-");
            mote = [parseInt(mote[0]), parseInt(mote[1])];
            var length = mote[1] - mote[0],
              empty = Array.apply(null, new Array(length + 1)).map(function () {
                return "";
              });
            splitText = splitText
              .slice(0, mote[0])
              .concat(empty)
              .concat(splitText.slice(mote[1] + 1, splitText.length));
            splitText.splice(mote[0], 0);
          }
        }
      }
      return splitText.join("");
    },
    isBTTV() {
      let emotes = this.betterttv;
      let globalemotes = this.betterttvglobalemotes;
      for (let index = 0; index < globalemotes.length; index++) {
        const element = globalemotes[index];
        this.betterttvemotes.push(element.code);
      }
      for (let index = 0; index < emotes.channelEmotes.length; index++) {
        const element = emotes.channelEmotes[index];
        this.betterttvemotes.push(element.code);
      }
      for (let index = 0; index < emotes.sharedEmotes.length; index++) {
        const element = emotes.sharedEmotes[index];
        this.betterttvemotes.push(element.code);
      }
    },
    readMessage(tags, msg) {
      if (this.ttson == true) {
        if (this.filterchat == true) {
          var list = this.filterwhitelist.split(", ");
          for (let index = 0; index < list.length; index++) {
            const element = list[index];
            if (!msg.includes(element)) {
              msg = this.removeEmotes(msg, tags["emotes"]);
              console.log(msg);
            }
          }
          for (let index = 0; index < this.betterttvemotes.length; index++) {
            const element = this.betterttvemotes[index];
            if (msg.includes(element)) {
              msg = msg.replace(element, "");
            }
          }
        }

        if (this.filterbots == true) {
          var bots = [
            "moobot",
            "nightbot",
            "fossabot",
            "streamelements",
            "streamlabs",
            "thepositivebot",
            "supibot",
            "wizebot",
          ];
          for (let index = 0; index < bots.length; index++) {
            const element = bots[index];
            if (tags["display-name"].toLowerCase() === element) {
              return null;
            }
          }
        }
        if (this.filterurl == true) {
          let b = msg.replace(/(?:https?|ftp):\/\/[\n\S]+/g, "");
          msg = b;
        }
        if (msg == "") return null;
        return this.speakClient(`${tags["display-name"]}: ${msg}`);
      }
      return null;
    },
    speakClient(text) {
      var msg = new SpeechSynthesisUtterance();
      var synth = window.speechSynthesis;
      msg.text = text;
      synth.speak(msg);
    },
    async getBETTERTTV() {
      let uri1 = `https://cybercentralchatapi.glitch.me/betterttv/${this.userid}`;
      let uri2 = `https://cybercentralchatapi.glitch.me/betterttvglobal`;
      try {
        const response1 = await axios.get(uri1);
        this.betterttv = response1.data;
        const response2 = await axios.get(uri2);
        this.betterttvglobalemotes = response2.data;

        this.isBTTV();
      } catch (error) {
        this.chatters = "API ERROR";
      }
    },
  },
  created() {
    this.reloadFunc();
    this.username = localStorage.getItem("username");
    if (this.username == "") return (this.username = null);
    this.userid = localStorage.getItem("userid");
    this.getBETTERTTV();
    this.fontsize = JSON.parse(localStorage.getItem("font"));
    this.readalertsfilter = JSON.parse(
      localStorage.getItem("readalertsfilter")
    );
    setInterval(() => {
      this.vueInsomnia().on();
      this.ttson = JSON.parse(localStorage.getItem("tts"));
      this.filterwhitelist = localStorage.getItem("filterwhitelist");
      this.filterchat = JSON.parse(localStorage.getItem("filter"));
      this.filterurl = JSON.parse(localStorage.getItem("filterurl"));
      this.filterbots = JSON.parse(localStorage.getItem("filterbots"));
    }, 1000);
  },
  computed: {
    msgStyles() {
      return {
        "font-size": `${this.fontsize}px`,
      };
    },
  },
  components: {
    viewercount,
    alerts,
  },
};
</script>

<style scoped>
ul{
  list-style-type: none; /* Remove bullets */
  padding-left: 10px;
  margin: 0; /* Remove margins */
}
</style>


