<template>
  <v-card>
    <v-list-item>
      <v-list-item-content>
        <v-list-item-title>CHAT: {{ username }}</v-list-item-title>
        <div v-if="showstats">
          <v-list-item-title>VIEWER: {{ chatters }}</v-list-item-title>
          <v-list-item-title>STATUS: {{ livestatus }}</v-list-item-title>
          <v-list-item-title>GAME: {{ game }}</v-list-item-title>
          <v-list-item-title class="text-wrap">TITLE: {{ title }}</v-list-item-title>
        </div>
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    showstats: "",
    username: "",
    userinfo: "",
    chatters: "",
    title: "",
    livestatus: "",
    game: "",
  }),
  methods: {
    setUpdate() {
      if (this.userinfo.stream == null) {
        this.livestatus = "OFFLINE";
        this.game = "NONE";
        this.title = "NONE";
        return null;
      }
      this.livestatus = this.userinfo.stream.stream_type.toUpperCase();
      this.title = this.userinfo.stream.channel.status;
      this.game = this.userinfo.stream.game;
    },
    async getUpdate() {
      let username = localStorage.getItem("username");
      let userid = localStorage.getItem("userid");
      let uri1 = `https://cybercentralchatapi.glitch.me/chatters/${username}`;
      let uri2 = `https://cybercentralchatapi.glitch.me/streaminfo/${userid}`;
      try {
        const response1 = await axios.get(uri1);
        this.username = localStorage.getItem("username").toUpperCase();
        this.chatters = response1.data.chatter_count;
      } catch (error) {
        this.chatters = "API ERROR";
      }
      if (this.showstats == true) {
        try {
          const response1 = await axios.get(uri2);
          this.userinfo = response1.data;
          this.setUpdate();
        } catch (error) {
          this.userinfo = "API ERROR";
        }
      }
    },
  },
  async created() {
    let username = localStorage.getItem("username");
    let showstats = JSON.parse(localStorage.getItem("showstats"));
    let userid = localStorage.getItem("userid");
    this.showstats = showstats;
    let uri1 = `https://cybercentralchatapi.glitch.me/chatters/${username}`;
    let uri2 = `https://cybercentralchatapi.glitch.me/streaminfo/${userid}`;
    try {
      const response1 = await axios.get(uri1);
      this.username = localStorage.getItem("username").toUpperCase();
      this.chatters = response1.data.chatter_count;
    } catch (error) {
      this.chatters = "API ERROR";
    }
    if (this.showstats == true) {
      try {
        const response1 = await axios.get(uri2);
        this.userinfo = response1.data;
        this.setUpdate();
      } catch (error) {
        this.userinfo = "API ERROR";
      }
    }
  },
  mounted() {
    setInterval(() => {
      this.getUpdate();
      if (this.showstats == true) {
        this.setUpdate();
      }
    }, 20000);
  },
};
</script>

