<template>
  <div>
    <div v-for="alert in this.alerts" v-bind:key="alert.message">
      <v-alert
        :value="alert.toggle"
        :color="alert.color"
        dark
        dismissible
        border="top"
        transition="scale-transition"
      >
        <h3 class="text-h5">{{ alert.type }}</h3>
        <div v-html="alert.message"></div>
      </v-alert>
    </div>
  </div>
</template>

<script>
import tmi from "tmi.js";
import alert from "../assets/alert.wav";
import connected from "../assets/connected.wav";
import disconnected from "../assets/disconnected.wav";
import emoteonlyon from "../assets/emoteonlyon.wav";
import emoteonlyoff from "../assets/emoteonlyoff.wav";
import { Howl, Howler } from "howler";
export default {
  data: () => ({
    alerts: [],
    message: "",
    username: "",
    alertsound: "",
  }),
  methods: {
    genAlert(type, message, color) {
      let constructor = {
        toggle: true,
        type: type,
        message: message,
        color: color,
      };
      let alerts = this.alerts;
      return alerts.push(constructor);
    },
  },
  created() {
    this.username = localStorage.getItem("username");
    this.alertsound = JSON.parse(localStorage.getItem("alertsound"));
    const opts = {
      channels: [this.username],
    };
    var client = new tmi.client(opts);

    client.connect();

    client.on("connected", (address, port) => {
      var connectedsound = new Howl({
        src: [connected],
      });
      if (this.alertsound) {
        connectedsound.play();
      }
    });

    client.on("disconnected", (reason) => {
      var disconnectedsound = new Howl({
        src: [disconnected],
      });
      if (this.alertsound) {
        disconnectedsound.play();
      }
    });

    client.on("emoteonly", (channel, enabled) => {
      if (this.alertsound) {
        if (enabled === true) {
          let enabledonsound = new Howl({
            src: [emoteonlyon],
          });
          enabledonsound.play();
        }
        if (enabled === false) {
          let enabledoffsound = new Howl({
            src: [emoteonlyoff],
          });
          enabledoffsound.play();
        }
      }
    });

    client.on(
      "subscription",
      (channel, username, method, message, userstate) => {
        let sound = new Howl({
          src: [alert],
        });
        this.genAlert("SUB", userstate["system-msg"], "red");
        if (this.alertsound) {
          sound.play();
        }
      }
    );

    client.on(
      "subgift",
      (channel, username, streakMonths, recipient, methods, userstate) => {
        let sound = new Howl({
          src: [alert],
        });
        this.genAlert("SUBGIFT", userstate["system-msg"], "red");
        if (this.alertsound) {
          sound.play();
        }
      }
    );

    client.on("cheer", (channel, userstate, message) => {
      let sound = new Howl({
        src: [alert],
      });
      let msg = `${userstate["display-name"]} just cheered ${userstate.bits}`;
      if (!message == "") {
        if (message.includes("cheer")) {
          for (let index = 1; index < 100000; index++) {
            message = message.replace("cheer" + index, "");
          }
          msg = `${userstate["display-name"]} just cheered ${userstate.bits} bits.<br>Message: ${message}`;
        }
      }
      this.genAlert("CHEER", msg, "red");
      if (this.alertsound) {
        sound.play();
      }
    });

    client.on(
      "resub",
      (channel, username, months, message, userstate, methods) => {
        let sound = new Howl({
          src: [alert],
        });
        this.genAlert("RESUB", userstate["system-msg"], "red");
        if (this.alertsound) {
          sound.play();
        }
      }
    );

    client.on("raided", (channel, username, viewers) => {
      let sound = new Howl({
        src: [alert],
      });
      this.genAlert(
        "RAID",
        `${username} just raided ${channel.replace(
          "#",
          ""
        )} with ${viewers} viewers!`,
        "red"
      );
      if (this.alertsound) {
        sound.play();
      }
    });
  },
};
</script>


