<template>
  <v-card>
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title>CHATTERS</v-list-item-title>
        <v-list-item-subtitle>{{ chatters }}</v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <hr />
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title>BROADCASTER</v-list-item-title>
        <v-list-item-subtitle
          v-for="streamer in broadcaster"
          v-bind:key="streamer"
          ><a :href="`https://www.twitch.tv/${streamer}`">{{
            streamer
          }}</a></v-list-item-subtitle
        >
      </v-list-item-content>
    </v-list-item>
    <hr />
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title>MODERATORS</v-list-item-title>
        <v-list-item-subtitle v-for="mod in moderators" v-bind:key="mod">
          <a :href="`https://www.twitch.tv/${mod}`">{{ mod }}</a>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <hr />
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title>VIPS</v-list-item-title>
        <v-list-item-subtitle v-for="vip in vips" v-bind:key="vip">
          <a :href="`https://www.twitch.tv/${vip}`">{{ vip }}</a>
        </v-list-item-subtitle>
      </v-list-item-content>
    </v-list-item>
    <hr />
    <v-list-item two-line>
      <v-list-item-content>
        <v-list-item-title>VIEWER</v-list-item-title>
        <v-list-item-subtitle v-for="viewer in viewers" v-bind:key="viewer"
          ><a :href="`https://www.twitch.tv/${viewer}`">{{
            viewer
          }}</a></v-list-item-subtitle
        >
      </v-list-item-content>
    </v-list-item>
  </v-card>
</template>

<script>
import axios from "axios";
export default {
  data: () => ({
    chatters: "",
    broadcaster: "",
    moderators: "",
    vips: "",
    viewers: "",
  }),
  methods: {
    async getUpdate() {
      let username = localStorage.getItem("username");
      let uri = `https://cybercentralchatapi.glitch.me/chatters/${username}`;
      try {
        const response = await axios.get(uri);
        this.chatters = response.data.chatter_count;
        this.broadcaster = response.data.chatters.broadcaster;
        this.moderators = response.data.chatters.moderators;
        this.vips = response.data.chatters.vips;
        this.viewers = response.data.chatters.viewers;
      } catch (error) {
        this.info = error.data;
      }
    },
  },
  async created() {
    let username = localStorage.getItem("username");
    let uri = `https://cybercentralchatapi.glitch.me/chatters/${username}`;
    try {
      const response = await axios.get(uri);
      this.chatters = response.data.chatter_count;
      this.broadcaster = response.data.chatters.broadcaster;
      this.moderators = response.data.chatters.moderators;
      this.vips = response.data.chatters.vips;
      this.viewers = response.data.chatters.viewers;
    } catch (error) {
      this.info = error.data;
    }
  },
  mounted() {
    setInterval(() => {
      this.getUpdate();
    }, 10000);
  },
};
</script>

